import React, { Component } from 'react';
import { Button, Form, Menu, Grid, Message, Image, Label, Segment, List } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';
import './App.css';
import logo from './logo.png';
import veronika from './veronika.jpg';
import jenny from './jenny.jpg';
import christian from './christian.jpg';

export default class App extends Component {

  state = { activeItem: 'Home' }

  handleItemClick = (e, { name }) => this.setState({ activeItem: name })

  render() {
    const { activeItem } = this.state;

    return (
      <Grid className='max-height'>
        <Grid.Row stretched>
          <Grid.Column width='4'>
            <Menu pointing secondary vertical >
              <Menu.Header><Image src={logo} /></Menu.Header>
              <Menu.Item name='Home' active={activeItem === 'Home'} onClick={this.handleItemClick} />
              <Menu.Item name='Find a Therapist' active={activeItem === 'Find a Therapist'} onClick={this.handleItemClick} />
              <Menu.Item name='Chat Room' active={activeItem === 'Chat Room'} onClick={this.handleItemClick} />
              <Menu.Item name='Supportive Resources' active={activeItem === 'Supportive Resources'} onClick={this.handleItemClick} />
              <Menu.Item name='Fast Facts' active={activeItem === 'Fast Facts'} onClick={this.handleItemClick} />
              <Menu.Item name='Coping with urges' active={activeItem === 'Coping with urges'} onClick={this.handleItemClick} />
            </Menu>
          </Grid.Column>
          <Grid.Column width='10' className='content' >
            <Message hidden={activeItem !== 'Home'} color='blue'>
              <Message.Header>Home</Message.Header>
              <Message.Content>
                Our mission is to increase awareness on self-harm among adolescents in Sweden. We will focus on providing a safe space where adolescents who have experienced self-harm or have urges can share their thoughts and seek assistance to overcome this. <p><b><i>Just like a happy thought made Peter Pan fly, so can you!</i></b></p>
              </Message.Content>
            </Message>
            <Message hidden={activeItem !== 'Find a Therapist'} positive color='blue'>
              <Message.Header>Find a Therapist</Message.Header>
              <Message.Content>Fill in the survey.</Message.Content>
            </Message>
            <Message hidden={activeItem !== 'Chat Room'} positive color='blue'>
              <Message.Header>Chat Room</Message.Header>
              <Message.Content>Share your experiences with people who understand.</Message.Content>
              <Label as='a' color='blue' image>
                <img src={veronika} />
                Veronika
      <Label.Detail>Friend</Label.Detail>
              </Label>
              <Label as='a' color='teal' image>
                <img src={jenny} />
                Jenny
      <Label.Detail>Friend</Label.Detail>
              </Label>
              <Label as='a' color='yellow' image>
                <img src={christian} />
                Christian
      <Label.Detail>Co-worker</Label.Detail>
              </Label>
              <Segment size='large' className='chat-window'>
                <Label as='a' color='blue' image><img src={veronika} />Veronika</Label>
                <Segment attached='top'>Hi, it's been a while.</Segment>
              </Segment>
              <Form>
                <Form.Group widths='equal'>
                  <Form.Input placeholder='Type your message' type='text' />
                  <Button color='blue' icon='send' />
                </Form.Group>
              </Form>
            </Message>
            <Message hidden={activeItem !== 'Supportive Resources'} positive color='blue'>
              <Message.Header>Supportive Resources</Message.Header>
              <Message.Content>
                <p>Do you know someone who self-harms? Discovering someone you love self-harms can be extremely upsetting. Although it is difficult to understand, it is important and useful to understand why people self-harm and learn strategies you can use to offer support.</p>
                <List.Header>How to recognize signs of self-harm:</List.Header>
                <Message.List>
                  <Message.Item>Scars from cuts or burns</Message.Item>
                  <Message.Item>Fresh cuts, scratches, or bruises</Message.Item>
                  <Message.Item>Increased isolation</Message.Item>
                  <Message.Item>Emotional instability</Message.Item>
                  <Message.Item>Keeping sharp objects on hand</Message.Item>
                  <Message.Item>Difficulties with relationships</Message.Item>
                  <Message.Item>Feelings of hopelessness or worthlessness</Message.Item>
                  <Message.Item>Wearing clothes that cover up the skin (such as long sleeves)</Message.Item>
                  <Message.Item>Blood stains on bedding or clothing</Message.Item>
                </Message.List>
                <br />
                <List.Header>Here are some supportive strategies:</List.Header>
                <Message.List>
                  <Message.Item>Do not be judgmental</Message.Item>
                  <Message.Item>Do not make the individual feel guilty about their behavior’s effect on others</Message.Item>
                  <Message.Item>Let the person know you want to listen, and you are there to hear them when they are ready to talk</Message.Item>
                  <Message.Item>Do not give an ultimatum</Message.Item>
                  <Message.Item>Understand it is a long and hard journey</Message.Item>
                </Message.List>

              </Message.Content>
            </Message>
            <Message hidden={activeItem !== 'Fast Facts'} positive color='blue'>
              <Message.Header>Fast Facts on Self-Harm in Sweden:</Message.Header>
              <Message.Content>
                <Message.List>
                  <Message.Item>Self-harm is a way of dealing with distress and overwhelming emotional pain.</Message.Item>
                  <Message.Item>Every person’s reason for self-harm is different. It is important to understand the meaning of self-harm for each individual.</Message.Item>
                  <Message.Item>Common ways of self-harm include intentionally cutting, burning or hitting oneself; scratching, interfering with wound healing, and breaking of bones</Message.Item>
                  <Message.Item>75% of those that self-harm use multiple methods</Message.Item>
                  <Message.Item>35.6% of adolescents in Sweden reported engaging in non-suicidal self-injury in at least once in the past year</Message.Item>
                </Message.List>
              </Message.Content>
            </Message>
            <Message hidden={activeItem !== 'Coping with urges'} positive color='blue'>
              <Message.Header>Coping with urges</Message.Header>
              <Message.Content>
                <Message.List>
                  <List.Header>If you self-harm to express pain and emotions:</List.Header>
                  <Message.Item>Write down any negative feelings and rip up the paper</Message.Item>
                  <Message.Item>Start a journal to express your feelings</Message.Item>
                  <Message.Item>Paint or draw</Message.Item>
                  <Message.Item>Sew, knit, or cross stitch </Message.Item>
                </Message.List>
                <br />
                <Message.List>
                  <List.Header>If you self-harm to calm and soothe yourself:</List.Header>
                  <Message.Item>Pet/cuddle a dog, cat, or other animal</Message.Item>
                  <Message.Item>Wrap yourself in a warm blanket</Message.Item>
                  <Message.Item>Listen to calming music</Message.Item>
                </Message.List>
                <br />
                <Message.List>
                  <List.Header>If you self-harm to feel disconnected or numb:</List.Header>
                  <Message.Item>Call a friend</Message.Item>
                  <Message.Item>Take a cold shower</Message.Item>
                  <Message.Item>Chew something with a very strong taste (chili peppers, grapefruit)</Message.Item>
                </Message.List>
                <br />
                <Message.List>
                  <List.Header>If you self-harm to release tension or anger:</List.Header>
                  <Message.Item>Exercise</Message.Item>
                  <Message.Item>Punch a pillow, cushion, or mattress</Message.Item>
                  <Message.Item>Squeeze a stress ball</Message.Item>
                  <Message.Item>Make noise (bang pots and pans) or play an instrument</Message.Item>
                  <Message.Item>Kick or throw a ball</Message.Item>
                  <Message.Item>Scream loud into an empty room or pillow</Message.Item>
                  <Message.Item>Pop bubble wrap</Message.Item>
                </Message.List>
                <br />
                <Message.List>
                  <List.Header>Substitute for cutting sensation:</List.Header>
                  <Message.Item>Snap rubber bands on wrists, arms, or legs instead of cutting or hitting</Message.Item>
                  <Message.Item>Rub ice across skin where you usually cut</Message.Item>
                </Message.List>
              </Message.Content>
            </Message>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row textAlign='center'>
          <Grid.Column>
            <Message info size='mini'>
              <Message.Header>Get Help Now!</Message.Header>
              <p>Crisis Hotline: (000) 000-0000</p>
            </Message>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}
